.phone {
    color: black;
    margin: 0 10px 0 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
    width: 150px;
    height: 200px;
}

.imagemPhone {
    width: 140px;
    height: 100px;
    background-color: #fff;
    object-fit: contain;
    border-radius: 2px;
    box-sizing: border-box;
    margin-bottom: 10px;
    padding: 8px;
    flex: 1;
}

.typePhone {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 10px;
    text-align: center;
}

.price {
    font-size: 14px;
    text-align: center;
}

.lista {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
}